export const getTokenURI = async (sn) => {
    if (!window.contract) {
      window.contract = await new web3.eth.Contract(contractABI, contractAddress);
    }
    var tokenURI  = await window.contract.methods.tokenURI(sn).call();
    console.log(tokenURI);
    return tokenURI;

}

